.item-enter {
  opacity: 0;
}
.item-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}
.item-exit {
  opacity: 1;
}
.item-exit-active {
  opacity: 0;
  transition: opacity 500ms ease-in;
}

.contacts-enter {
  opacity: 0;
}
.contacts-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}
.contacts-exit {
  opacity: 1;
}
.contacts-exit-active {
  opacity: 0;
  transition: opacity 500ms ease-in;
}
